import { css } from "styled-components";

export interface TextfieldFrameInputStyleProps {
  $error?: boolean;
  $errorColor?: React.CSSProperties["borderColor"];
}

export const textfieldFrameInputStyle = css<TextfieldFrameInputStyleProps>`
  outline: none;
  padding: 0 12px;
  ${(p) => p.theme.fonts["Body-Medium/Regular"]};
  border: 1px solid ${(p) => p.theme.colors.divider};
  border-radius: ${(p) => p.theme.radius.xxxxs}px;
  color: ${(p) => p.theme.colors.textInput.default};
  caret-color: ${(p) => p.theme.colors.textInput.link};

  &::selection {
    background: ${(p) => p.theme.colors.button.hovered};
  }

  &::placeholder {
    color: ${(p) => p.theme.colors.textInput.minor};
  }

  &:hover,
  &:focus {
    border-color: ${(p) => p.theme.colors.textInput.link};
  }

  /* error styles */
  ${({ $error, $errorColor, theme: { colors } }) =>
    !!$error &&
    css`
      border-color: ${$errorColor || colors.baseColor.Volcano5};

      &:hover,
      &:focus {
        border-color: ${$errorColor || colors.baseColor.Volcano5};
      }
    `}

  /* disable styles */
  &:disabled {
    color: ${(p) => p.theme.colors.buttonInput.disabled};
    border-color: ${(p) => p.theme.colors.button.disabled};
    background-color: transparent;

    &:hover,
    &:focus {
      border-color: ${(p) => p.theme.colors.button.disabled};
    }
  }
`;
